.features__wrapper {
  padding-left: calc((123 / 1200) * 100) + 0%;
  padding-right: calc((123 / 1200) * 100) + 0%;
  padding-top: 108px;
  background: #f8f9fc;

  @include vp-1199 {
    padding-left: $padding-tbl;
    padding-right: $padding-tbl;
    padding-bottom: 93px;
    padding-top: 93px;
  }

  @include vp-767 {
    padding-left: $padding-mbl;
    padding-right: $padding-mbl;
    padding-bottom: 61px;
    padding-top: 63px;
  }
}

.features h2 {
  margin: 0;
  color: #102654;
  font-family: $ff-as;
  font-size: 3.38em;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  text-align: center;

  @include vp-1199 {
    font-size: 2.63em;
  }

  @include vp-767 {
    font-size: 2em;
    letter-spacing: 0.96px;
  }
}

.features__list {
  list-style-type: none;
  display: grid;
  grid-template-areas:
    "b a c"
    "b a c";
  margin: 0;
  padding-top: 21px;

  @include vp-1199 {
    grid-template-areas:
      "w b"
      "a ."
      "c d";
    position: relative;
    padding: 0;
    column-gap: 44px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.features__item {
  color: #102654;
  font-family: $ff-rc;
  position: relative;
  display: flex;
  gap: calc((105 / 1200) * 100) + 0%;
  padding-top: 5px;
  margin-bottom: 125px;

  @include vp-1199 {
    position: relative;
    padding: 25px 0 0 0;
    gap: calc((65 / 768) * 100) + 0%;
    margin-bottom: 0;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
}

.features__item--line::after {
  content: "";
  position: absolute;
  border: 2px solid #d10a42;
  top: 185px;
  width: 340px;
  opacity: 0.2;

  @include vp-1199 {
    display: none;
  }
}

.features__item--b {
  @include vp-1199 {
    grid-area: b;
    margin-bottom: 282px;
  }

  @include vp-767 {
    padding-top: 36px;
    padding-bottom: 43px;
    margin-bottom: 0;
  }
}

.features__item--c {
  @include vp-1199 {
    grid-area: c;
  }

  @include vp-767 {
    padding-top: 46px;
  }
}

.features__item--w {
  @include vp-1199 {
    grid-area: w;
    margin-bottom: 282px;
  }

  @include vp-767 {
    padding-top: 50px;
    margin-bottom: 0;
  }
}

.features__item--d {
  @include vp-1199 {
    grid-area: d;

    @include vp-767 {
      padding-top: 35px;
    }
  }
}

.features__item--rigth {
  padding-left: calc((135 / 1200) * 100) + 0%;

  @include vp-1199 {
    padding-left: 0;
  }
}

.features__text {
  padding-right: calc((65 / 1200) * 100) + 0%;

  @include vp-1199 {
    padding: 0;
  }

  @include vp-767 {
    text-align: center;
  }
}

.features__header {
  margin: 0;
  padding-top: 28px;
  font-size: 1.38em;
  font-style: normal;
  font-weight: 700;

  @include vp-1199 {
    font-size: 1.25em;
    padding-top: 18px;
  }

  @include vp-767 {
    padding-top: 9px;
  }
}

.features__description {
  margin: 0;
  padding-top: 4px;
  padding-right: calc((26 / 1200) * 100) + 0%;
  font-size: 1em;
  font-style: normal;

  @include vp-767 {
    padding-top: 5px;
    padding-right: 0;
  }
}

.features__description br {
  @include vp-1199 {
    display: none;
  }

  @include vp-767 {
    display: block;
  }
}

.features__number {
  margin: 0;
  padding: 0;
  font-size: 4.44em;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  background: linear-gradient(180deg, #d10a42 0%, #0041ca 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-1199 {
    font-size: 3.7em;
  }

  @include vp-767 {
    line-height: 76%;
  }
}

.features__unit {
  position: absolute;
  margin: 0;
  padding: 0;
  color: #2e35ac;
  font-size: 0.88em;
  font-style: normal;
  font-weight: 400;
  top: 87px;

  @include vp-1199 {
    top: 92px;
  }

  @include vp-767 {
    position: static;
  }
}

.features__item--center {
  display: block;
  padding: 0;
  color: $color-default-white;
  background: #1c3374;
  grid-area: a;
  width: 240px;
  height: 307px;
  text-align: center;
  margin-top: calc((184 / 1200) * 100) + 0%;
  margin-left: calc((-65 / 1200) * 100) + 0%;

  @include vp-1199 {
    margin: 0;
    height: auto;
    position: absolute;
    top: -240px;
    width: 442px;
    left: calc((297 / 768) * 100) + 0%;
  }

  @include vp-767 {
    position: static;
    width: 320px;
  }
}

.features__number-center {
  margin: 0;
  font-size: 4.63em;
  font-style: normal;
  font-weight: 700;
  color: $color-default-white;
  padding: 79px 0 0 0;

  @include vp-1199 {
    padding-top: 58px;
  }
}

.features__header--center {
  margin: 0;
  padding: 0;
  font-size: 1.63em;
  font-style: normal;
  font-weight: 700;
  padding-top: 35px;

  @include vp-1199 {
    font-size: 1.37em;
    padding-top: 30px;
  }
}

.features__description--center {
  margin: 0;
  text-align: center;
  font-size: 1em;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  padding-left: calc((34 / 1200) * 100) + 0%;
  padding-right: calc((34 / 1200) * 100) + 0%;
  padding-top: 39px;

  @include vp-1199 {
    padding-left: calc((220 / 768) * 100) + 0%;
    padding-right: calc((220 / 768) * 100) + 0%;
    padding-top: 17px;
    padding-bottom: 38px;
  }

  @include vp-767 {
    padding-left: calc((74 / 320) * 100) + 0%;
    padding-right: calc((74 / 320) * 100) + 0%;
    padding-bottom: 43px;
    padding-top: 12px;
  }
}
